"use strict";

/* Start component
----------------------------------- */
var StarsFrame = React.createClass({

  render: function render() {
    var stars = [];
    for (var i = 0; i < this.props.numberOfStars; i++) {
      stars.push(React.createElement("span", { key: i, className: "glyphicon glyphicon-star" }));
    }
    return React.createElement(
      "div",
      { id: "stars-frame" },
      React.createElement(
        "div",
        { className: "well" },
        stars
      )
    );
  }

});

/* Button component
----------------------------------- */
var ButtonFrame = React.createClass({

  render: function render() {
    var disabled,
        button,
        correct = this.props.correct;

    switch (correct) {
      case true:
        button = React.createElement(
          "button",
          { className: "btn btn-success",
            onClick: this.props.acceptAnswer },
          React.createElement("span", { className: "glyphicon glyphicon-ok" })
        );
        break;
      case false:
        button = React.createElement(
          "button",
          { className: "btn btn-danger" },
          React.createElement("span", { className: "glyphicon glyphicon-remove" })
        );
        break;
      default:
        disabled = this.props.selectedNumbers.length === 0;
        button = React.createElement(
          "button",
          { className: "btn btn-primary", disabled: disabled,
            onClick: this.props.checkAnswer },
          "="
        );
    }

    return React.createElement(
      "div",
      { id: "button-frame" },
      button,
      React.createElement(
        "button",
        { className: "btn btn-warning btn-xs refresh", onClick: this.props.redraw,
          disabled: this.props.redraws === 0 },
        React.createElement("span", { className: "glyphicon glyphicon-refresh" }),
        "\xA0",
        this.props.redraws
      )
    );
  }

});

/* Answer component
----------------------------------- */
var AnswerFrame = React.createClass({

  render: function render() {
    var props = this.props;
    var selectedNumbers = props.selectedNumbers.map(function (i) {
      return React.createElement(
        "span",
        { key: i, onClick: props.unselectNumber.bind(null, i) },
        i
      );
    });

    return React.createElement(
      "div",
      { id: "answer-frame" },
      React.createElement(
        "div",
        { className: "well" },
        selectedNumbers
      )
    );
  }

});

/* Numbers frame
----------------------------------- */
var NumbersFrame = React.createClass({

  render: function render() {

    var numbers = [],
        className,
        selectNumber = this.props.selectNumber,
        selectedNumbers = this.props.selectedNumbers,
        usedNumbers = this.props.usedNumbers;
    for (var i = 0; i <= 9; i++) {
      className = "number selected-" + (selectedNumbers.indexOf(i) >= 0); // see if the number it is in the array
      className += " used-" + (usedNumbers.indexOf(i) >= 0);
      numbers.push(React.createElement(
        "div",
        { key: i, className: className, onClick: selectNumber.bind(null, i) },
        i
      ));
    }
    return React.createElement(
      "div",
      { id: "numbers-frame" },
      React.createElement(
        "div",
        { className: "well" },
        numbers
      )
    );
  }

});

/* Game done component
----------------------------------- */
var DoneFrame = React.createClass({
  render: function render() {
    return React.createElement(
      "div",
      { className: "well text-center" },
      React.createElement(
        "h2",
        null,
        this.props.doneStatus
      )
    );
  }
});

/* Main component
----------------------------------- */
var Game = React.createClass({

  getInitialState: function getInitialState() {
    return {
      numberOfStars: this.randomNumber(),
      selectedNumbers: [],
      usedNumbers: [],
      redraws: 5,
      correct: null,
      doneStatus: null
    };
  },

  randomNumber: function randomNumber() {
    return Math.floor(Math.random() * 9) + 1;
  },

  selectNumber: function selectNumber(clickedNumber) {
    if (this.state.selectedNumbers.indexOf(clickedNumber) < 0) {
      this.setState({
        selectedNumbers: this.state.selectedNumbers.concat(clickedNumber),
        correct: null
      });
    }
  },

  unselectNumber: function unselectNumber(clickedNumber) {
    var selectedNumbers = this.state.selectedNumbers,
        indexOfNumber = selectedNumbers.indexOf(clickedNumber);

    selectedNumbers.splice(indexOfNumber, 1);

    this.setState({ selectedNumbers: selectedNumbers, correct: null });
  },

  sumOfSelectedNumbers: function sumOfSelectedNumbers() {
    return this.state.selectedNumbers.reduce(function (p, n) {
      return p + n;
    }, 0);
  },

  checkAnswer: function checkAnswer() {
    var correct = this.state.numberOfStars == this.sumOfSelectedNumbers();
    this.setState({ correct: correct });
    console.log(this.sumOfSelectedNumbers());
    console.log(correct);
  },

  acceptAnswer: function acceptAnswer() {
    var usedNumbers = this.state.usedNumbers.concat(this.state.selectedNumbers);
    this.setState({
      selectedNumbers: [],
      usedNumbers: usedNumbers,
      correct: null,
      numberOfStars: this.randomNumber()
    });
  },

  redraw: function redraw() {
    if (this.state.redraws > 0) {
      this.setState({
        numberOfStars: this.randomNumber(),
        correct: null,
        selectedNumbers: [],
        redraws: this.state.redraws - 1
      });
    }
  },

  render: function render() {
    var numberOfStars = this.state.numberOfStars,
        selectedNumbers = this.state.selectedNumbers,
        usedNumbers = this.state.usedNumbers,
        correct = this.state.correct,
        checkAnswer = this.checkAnswer,
        redraws = this.state.redraws,
        doneStatus = this.state.doneStatus,
        bottomFrame;

    if (doneStatus) {
      bottomFrame = React.createElement(DoneFrame, { doneStatus: doneStatus });
    } else {
      bottomFrame = React.createElement(NumbersFrame, { selectedNumbers: selectedNumbers,
        usedNumbers: usedNumbers,
        selectNumber: this.selectNumber });
    }

    return React.createElement(
      "div",
      { id: "game" },
      React.createElement(
        "h2",
        null,
        "Play game!"
      ),
      React.createElement("hr", null),
      React.createElement(
        "div",
        { className: "clearfix" },
        React.createElement(StarsFrame, { numberOfStars: numberOfStars }),
        React.createElement(ButtonFrame, { selectedNumbers: selectedNumbers,
          correct: correct,
          checkAnswer: checkAnswer,
          acceptAnswer: this.acceptAnswer,
          redraw: this.redraw,
          redraws: redraws }),
        React.createElement(AnswerFrame, { selectedNumbers: selectedNumbers,
          unselectNumber: this.unselectNumber })
      ),
      bottomFrame
    );
  }

});

/* Render
----------------------------------- */
